html {
  --bg: #efefef;
  --fg: #1e1e1e;
  --gray: #555;
  --light-gray: #ccc;
  --shadow: #e6e6e6;
  --success: #5ace84;
  --primary: #ff585d;
  --secondary: #0072ce;
  background-color: var(--bg);
  color: var(--fg);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif;
  font-size: calc(.9em + .5vw);
  line-height: 1.3;
}

body {
  margin: 0;
  padding: 1em;
}

main {
  max-width: 30em;
  text-align: justify;
  margin: 0 auto;
}

h1, h5 {
  text-align: center;
  background-image: url("logo-black.54439fde.svg");
  background-position: 50% 1em;
  background-repeat: no-repeat;
  background-size: auto 1.5em;
  margin-top: 0;
  padding: 3.5em 0 .5em;
}

.link {
  color: var(--primary);
  text-decoration: none;
}

.link:hover, .link:focus {
  text-decoration: underline;
}

.link:active {
  color: var(--secondary);
}

button, input {
  font: inherit;
  outline: none;
}

button {
  background-color: var(--secondary);
  color: #efefef;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: .3em .75em;
  transition: -webkit-transform 30ms, transform 30ms;
}

button:hover, button:focus {
  box-shadow: inset 0 0 10em rgba(255, 255, 255, .2);
}

button:active {
  box-shadow: inset 0 0 10em rgba(0, 0, 0, .1);
}

button.link {
  box-shadow: none;
  background: none;
  border: none;
  display: inline;
}

[disabled] button, button[disabled] {
  box-shadow: none;
  background-color: var(--light-gray);
  color: #525151;
  cursor: not-allowed;
  -webkit-transform: none;
  transform: none;
}

[disabled] button {
  text-indent: -900em;
  width: 2em;
  position: relative;
}

[disabled] button:after {
  content: " ";
  width: .8em;
  height: .8em;
  border: 2px solid #fff;
  border-color: var(--fg) transparent var(--fg) transparent;
  border-radius: 50%;
  -webkit-animation: 1.2s linear infinite loader;
  animation: 1.2s linear infinite loader;
  display: block;
  position: absolute;
  top: .45em;
  right: .5em;
}

@-webkit-keyframes loader {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes loader {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

fieldset {
  border: none;
  padding: 0;
}

input {
  background-color: var(--shadow);
  caret-color: var(--primary);
  color: inherit;
  border: none;
  border-radius: 5px 0 0 5px;
  padding: .25em 1em;
}

input::selection {
  background-color: var(--secondary);
  color: #efefef;
}

input:focus {
  box-shadow: inset 0 0 10em rgba(0, 0, 0, .02);
}

code {
  color: var(--gray);
}

li {
  padding-bottom: 1em;
}

.bike {
  width: 560px;
  background-color: #dcdcdc;
  border-radius: 30px;
  margin: 20px auto;
  padding: 5px;
}

.bike_img {
  margin-top: 20px;
  margin-left: 10px;
}

.bike_index {
  margin-top: 30px;
  margin-left: 10px;
  margin-right: 10px;
}

.bike button {
  width: 110px;
  height: 50px;
  background-color: #4682b4;
  border-radius: 5px;
  margin: 20px;
}

.balance_content {
  width: 400px;
  background-color: #dcdcdc;
  border-radius: 10px;
  margin: 10px auto;
}

.balance_content button {
  background-color: teal;
  display: block;
}

.balance_content button, fieldset {
  width: 100%;
}

.balance_content p {
  width: 200px;
  margin: 0 auto;
}

